$font-size-code: 1rem;
$font-size-text: 1.3rem;
$font-size-heading1: 6rem;
$font-size-heading2: 3rem;
$font-size-heading3: 2.2rem;
$font-size-upto-xs-scaling: 0.6;
$font-size-upto-md-scaling: 0.75;
$font-size-logo: 2rem;

$line-height-text: 2rem;

$padding-dashboard-li: 5px;

@mixin breakpoint($class) {
  @if $class == xxs {
    @media (max-width: 500px) {
      @content; } }
  @else if $class == xs {
    @media (max-width: 767px) {
      @content; } }
  @else if $class == sm {
    @media (min-width: 768px) {
      @content; } }
  @else if $class == md {
    @media (min-width: 992px) {
      @content; } }
  @else if $class == lg {
    @media (min-width: 1300px) {
      @content; } }
  @else {
    @warn "Breakpoint mixin supports: xs, sm, md, lg"; } }
